body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0px;padding:0px}.home{height:90vh}.shop{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}@media only screen and (min-width: 600px){.shop{flex-direction:row;justify-content:space-evenly}}.navbar{border-bottom:1px solid gray;display:flex;width:90%;justify-content:space-between}.navbar .logo{width:15%;display:flex;justify-content:center;font-family:"Kalam";font-size:0.7em}.navbar .navbar-items{width:60%;display:flex;justify-content:space-evenly}@media only screen and (min-width: 600px){.navbar{width:100%}.navbar .logo{width:10%;font-size:1em}.navbar .navbar-items{width:40%}}@font-face{font-family:"Kalam";font-style:normal;font-weight:300;font-display:swap;src:local("Kalam Light"),local("Kalam-Light"),url(https://fonts.gstatic.com/s/kalam/v10/YA9Qr0Wd4kDdMtD6GjLMkiQ.woff2) format("woff2");unicode-range:U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD}.jumbotron-content{top:200px;position:absolute;display:flex;flex-direction:column;justify-content:center;align-items:center;text-shadow:1px 1px black;width:100%;z-index:1}.jumbotron-content p{max-width:800px;text-align:center;font-size:large;height:100px;word-wrap:break-word;white-space:wrap;color:white;font-family:"Kalam"}.jumbotron-content h1{color:white;font-family:"Kalam"}.jumbotron{background-color:rgba(0,0,0,0.5);height:60vh;width:100%;position:relative}.jumbotron .layer{background-color:rgba(0,0,0,0.5);position:absolute;top:0;left:0;width:100%;height:100%}.item{position:relative;display:inline-flex;flex-direction:column;width:300px}.item .title{display:flex;justify-content:center;text-align:left;height:55px}.item .title h1{margin-top:20px;border-radius:5px;font-size:1.2em;font-family:"Kalam";width:100%}.item .picture{display:flex;justify-content:center;align-items:center}.item .picture div{width:250px;height:300px}.item .picture div:hover{opacity:0.5}.item .price{display:flex;flex-direction:column;justify-content:center;align-items:left}.item .price p{font-size:1em;font-weight:bold;font-family:"Kalam"}.gallery{width:650px;max-height:600px;padding:0px 50px;z-index:1}.gallery .picture{height:250px;width:300px}@media only screen and (min-width: 600px){.gallery{width:650px;max-height:600px;padding:0px 50px;z-index:1}.gallery .picture{height:600px;width:650px}}.specification{height:100vh;display:flex;flex-direction:column}.specification .back-to-shopping{position:absolute;left:10px;top:130px}.specification .title-spec{display:flex;justify-content:center;align-items:center;margin-bottom:20px}.specification .title-spec h1{font-family:"Kalam"}.specification .product-spec{display:flex;flex-direction:column;width:100%;margin:auto;padding:40px;box-shadow:10px 10px 25px gray}.specification .product-spec .picture-spec{display:flex;width:100%;justify-content:center;align-items:center}.specification .product-spec .detail-spec{display:flex;flex-direction:column;justify-content:center;align-items:center}.specification .product-spec .detail-spec a{background:rgba(2,0,251,0.7);text-decoration:none;color:white;padding:10px;margin:5px;border-radius:5px}@media only screen and (min-width: 600px){.specification{height:100vh;display:flex;flex-direction:column}.specification .back-to-shopping{position:absolute;left:50px}.specification .product-spec{display:flex;flex-direction:row;width:80%;margin:auto;padding:40px;box-shadow:10px 10px 25px gray}.specification .product-spec .picture-spec{display:flex;width:60%;justify-content:flex-end}.specification .product-spec .detail-spec{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:40%}.specification .product-spec .detail-spec a{background:rgba(2,0,251,0.7);text-decoration:none;color:white;padding:10px;margin:5px;border-radius:5px}}.banner{position:absolute;top:50%}.banner h2{width:100%;padding:10px;z-index:1;background:red;color:white}.title{display:flex;height:15vh;justify-content:center;align-items:center;font-family:"kalam"}.footer{display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end;height:25vh;background:rgba(128,128,128,0.2)}.footer .footer-links{width:100%;height:15vh;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end;border-top:1px solid gray;font-size:0.5em}@media only screen and (min-width: 600px){.footer .footer-links{font-size:1em;padding:0px 150px}}.collection-item{margin:50px}.collection-item .collection-item-background{height:200px;width:200px;background-position:center;background-size:cover}.collection-item .collection-item-title{display:flex;justify-content:center;align-items:center;font-family:"kalam";font-weight:800;color:white;position:absolute;height:200px;width:200px;text-align:center}.collection{position:relative;display:flex;justify-content:space-evenly}

